import React, { Component } from "react";
import { Container } from "react-bootstrap";
import Tabs from "react-bootstrap/Tabs";
import Tab from "react-bootstrap/Tab";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Card from "react-bootstrap/Card";
import Accordion from "react-bootstrap/Accordion";

class Bungalow extends Component {
  render() {
    return (
      <Container>
        <Accordion className="mb-2 text-muted">
          <Accordion.Item eventKey="0">
            <Accordion.Header>
              <span className="mb-2 text-muted">Tenancy conditions</span>
            </Accordion.Header>
            <Accordion.Body className="mb-2 text-muted">
              <ul>
                <li>Pay the rent on time.</li>
                <li>Keep the premises reasonably clean and tidy.</li>
                <li>
                  Notify the landlord as soon as any repairs are needed. The
                  tenant(s) must not stop paying rent while waiting for repairs
                  to be done.
                </li>
                <li>
                  If a maximum number of occupants is stated in the tenancy
                  agreement, that number must not be exceeded.
                </li>
                <li>Use the premises primarily for residential purposes.</li>
                <li>
                  Pay all electricity, gas, telephone and metered water charges.
                </li>
                <li>
                  Not damage or allow damage to the premises, and inform the
                  landlord of any damage as soon as possible.
                </li>
                <li>
                  Not disturb the neighbours or the landlord’s other tenants.
                </li>
                <li>
                  Not alter the premises without the landlord’s written consent.
                </li>
                <li>Not use the property for any unlawful purpose.</li>
                <li>
                  Meet all other requirements of the Residential Tenancies Act,
                  as well as any other relevant and applicable laws.
                </li>
                <li>
                  Leave the property reasonably clean and tidy, and clear of
                  rubbish and possessions at the end of the tenancy.
                </li>
                <li>
                  Leave all keys or similar with the landlord at the end of the
                  tenancy.
                </li>
                <li>Leave all chattels supplied with the tenancy.</li>
              </ul>
            </Accordion.Body>
          </Accordion.Item>
          <Accordion.Item eventKey="1">
            <Accordion.Header>
              <span className="mb-2 text-muted">Subletting and assignment</span>
            </Accordion.Header>
            <Accordion.Body className="mb-2 text-muted">
              <ul>
                <li>
                  The tenant must not sublet the tenancy or part with possession
                  (excluding assignment). Assignment is permitted only with the
                  landlord’s written consent. Consent may not be unreasonably
                  withheld. The tenant shall not use the premises for any type
                  of short-term accommodation purposes without the written
                  permission of the landlord.
                </li>
              </ul>
            </Accordion.Body>
          </Accordion.Item>
          <Accordion.Item eventKey="2">
            <Accordion.Header>
              <span className="mb-2 text-muted">Pets</span>
            </Accordion.Header>
            <Accordion.Body className="mb-2 text-muted">
              <ul>
                <li>Pets are not permitted at this rental property.</li>
              </ul>
            </Accordion.Body>
          </Accordion.Item>
        </Accordion>
        <br />
        <Tabs
          defaultActiveKey="front"
          transition={false}
          id="noanim-tab-example"
          className="mb-3"
        >
          <Tab eventKey="front" title="Front">
            <Row xs={1} md={2} className="g-4">
              <Col>
                <Card>
                  <Card.Img
                    variant="top"
                    src="https://lh3.googleusercontent.com/2mWtt-Wgsv9Y09Qh-swyIJqa0KiKYvWrg_roROadQWH6gKvpZH0SI_QWJ1o1SMIHq8xrycyhK3fW22u6Apo4kXXY9I8GT7wTBExmx1JTXx4KvFvQWKB9x6bEBxM9Eptwnnaa2CM5=w2400"
                  />
                  <Card.Body>
                    <Card.Subtitle className="mb-2 text-muted">
                      Corner view 1
                    </Card.Subtitle>
                  </Card.Body>
                </Card>
              </Col>
              <Col>
                <Card>
                  <Card.Img
                    variant="top"
                    src="https://lh3.googleusercontent.com/-ZdpH9ZyeReqyzmF6UrUDpBSXsxL1LWBhr77ymb0nhlwVZ_OVGDEVxt7XoP-E3jvhtCDxWtszPBmOdwx9N3RhvQLG_zmuM8baCwdLyJeYagQvQQGwzln5eknRztmJ8dOPD0ULmEI=w2400"
                  />
                  <Card.Body>
                    <Card.Subtitle className="mb-2 text-muted">
                      Corner view 2
                    </Card.Subtitle>
                  </Card.Body>
                </Card>
              </Col>
              <Col>
                <Card>
                  <Card.Img
                    variant="top"
                    src="https://lh3.googleusercontent.com/etKJPHeC_CZF3sox-jkzj3m1kGuqUsp2Sn75BcLyzYwmNDOud0f-2vY15BrjKFn7VolObjd3nEFAxZOLN0Xhh-N7ZrDFkzIHTFmtxjM69lV3k_GJQmwbwjcoYXeQymKiYBkzsu8z=w2400"
                  />
                  <Card.Body>
                    <Card.Subtitle className="mb-2 text-muted">
                      Spacious front area
                    </Card.Subtitle>
                    <Card.Text className="mb-2 text-muted">
                      Open garage leading to main entrance.
                    </Card.Text>
                  </Card.Body>
                </Card>
              </Col>
              <Col>
                <Card>
                  <Card.Img
                    variant="top"
                    src="https://lh3.googleusercontent.com/j59plRCKzcVxluSWvEpiv642M0F7qF1QuZozefr_QLtMEcxV5zMj1TqdNswZGYJQj8elMdgqnHe9DPi7hMx1pvskJpKvAvkUWLt3K91WQ1aUFfBIiji5DY0HRRUwLJ-srLTygJNg=w2400"
                  />
                  <Card.Body>
                    <Card.Subtitle className="mb-2 text-muted">
                      South west entrance
                    </Card.Subtitle>
                    <Card.Text className="mb-2 text-muted">
                      Entrance from Bur Oak street.
                    </Card.Text>
                  </Card.Body>
                </Card>
              </Col>
            </Row>
          </Tab>
          <Tab eventKey="living" title="Living and dining Area">
            {" "}
            <Row xs={1} md={2} className="g-4">
              <Col>
                <Card>
                  <Card.Img
                    variant="top"
                    src="https://lh3.googleusercontent.com/aTRUW1T1C4cGYdzA1DYTUaMb66vGu19wBU-Q0Kp111kl0Gpnagd-PhfnKsyplUSvZW2ntdsLA1tJBISu9tWxmdva6PN-2TFZ6x8ttXZPfD4SBI0V00NMN0FANjO7nF5TOhPScZJK=w2400"
                  />
                  <Card.Body>
                    <Card.Subtitle className="mb-2 text-muted">
                      Furnished living and dining area
                    </Card.Subtitle>
                    <Card.Text className="mb-2 text-muted">
                      <ul>
                        <li>50-inch smart tv</li>
                        <li>Fridge</li>
                        <li>4-seater dining table</li>
                        <li>Window curtains</li>
                      </ul>
                    </Card.Text>
                  </Card.Body>
                </Card>
              </Col>
            </Row>
          </Tab>
          <Tab eventKey="kitchen1" title="Kitchen 1"></Tab>
          <Tab eventKey="shower" title="Shower"></Tab>
          <Tab eventKey="bedroom1" title="Bedroom 1"></Tab>
          <Tab eventKey="bedroom2" title="Bedroom 2"></Tab>
          <Tab eventKey="bedroom3" title="Bedroom 3"></Tab>
          <Tab eventKey="laundry" title="Laundry Area"></Tab>
          <Tab eventKey="kitchen2" title="Kitchen 2"></Tab>
        </Tabs>
      </Container>
    );
  }
}

export default Bungalow;
