import React, { Component } from "react";
import { Container } from "react-bootstrap";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Card from "react-bootstrap/Card";
import Badge from "react-bootstrap/Badge";

class Townhouse extends Component {
  render() {
    return (
      <Container>
        <Row xs={1} md={3} className="g-4">
          <Col>
            <Card>
              <Card.Img
                variant="top"
                src="https://lh3.googleusercontent.com/j59plRCKzcVxluSWvEpiv642M0F7qF1QuZozefr_QLtMEcxV5zMj1TqdNswZGYJQj8elMdgqnHe9DPi7hMx1pvskJpKvAvkUWLt3K91WQ1aUFfBIiji5DY0HRRUwLJ-srLTygJNg=w2400"
              />
              <Card.Body>
                <Card.Subtitle className="mb-2 text-muted">
                  Bungalow townhouse <Badge bg="danger">Not available</Badge>
                </Card.Subtitle>
                <Card.Text className="mb-2 text-muted">
                  Located in Savannah subdivision - Fully furnished short or
                  long term accomodation, corner lot near Glen A entrance
                </Card.Text>
                <Card.Text className="mb-2 text-muted">
                  3 bedrooms - 1 toilet - 1 car space
                </Card.Text>
                <Card.Link href="/bungalow">More details</Card.Link>
              </Card.Body>
            </Card>
          </Col>
          <Col>
            <Card>
              <Card.Body>
                <Card.Subtitle className="mb-2 text-muted">
                  2-storey townhouse{" "}
                  <Badge bg="secondary">Under construction</Badge>
                </Card.Subtitle>
                <Card.Text className="mb-2 text-muted">
                  Located in Lessandra C subdivision - Fully furnished short or
                  long term accomodation
                </Card.Text>
                <Card.Text className="mb-2 text-muted">
                  3 bedrooms - 1 toilet
                </Card.Text>
                <Card.Link href="#">More details</Card.Link>
              </Card.Body>
            </Card>
          </Col>
          <Col>
            <Card>
              <Card.Body>
                <Card.Subtitle className="mb-2 text-muted">
                  2-storey townhouse{" "}
                  <Badge bg="secondary">Under construction</Badge>
                </Card.Subtitle>
                <Card.Text className="mb-2 text-muted">
                  Located in Lessandra C subdivision - Fully furnished airbnb
                  accomodation
                </Card.Text>
                <Card.Text className="mb-2 text-muted">
                  3 bedrooms - 1 toilet - 1 car space
                </Card.Text>
                <Card.Link href="#">More details</Card.Link>
              </Card.Body>
            </Card>
          </Col>
        </Row>
      </Container>
    );
  }
}

export default Townhouse;
